@media screen and (max-width: 425px) { .section-venture form { padding-left: 2rem; padding-right: 2rem; padding-top: 2rem; }
  .section-venture form .form-title:first-child { display: none; } }

.section-venture form .form-group .col-sm-6 { margin-bottom: 2.5rem; }

.section-venture form .button { max-width: 39rem; width: 100%; }

@media screen and (max-width: 1024px) { .section-venture form .button { max-width: 30rem; } }

@media screen and (max-width: 768px) { .section-venture form .button { height: 7.5rem; margin-left: auto; margin-right: auto; } }

@media screen and (max-width: 425px) { .section-venture form .button { margin-top: 3.5rem; min-width: 0; } }

@media screen and (max-width: 425px) { .section-venture .group-field { display: none; } }

body:not(.home) .section-venture { position: relative; }

@media screen and (max-width: 768px) { body:not(.home) .section-venture { background: -webkit-linear-gradient(33deg, #520596 0%, #fc486b 70%); background: linear-gradient(33deg, #520596 0%, #fc486b 70%); }
  body:not(.home) .section-venture:before { background: url(../img/planets_form_768.png) no-repeat center -4.5rem; position: absolute; top: 0; bottom: 0; left: 0; content: ''; width: 100%; } }

@media screen and (max-width: 425px) { body:not(.home) .section-venture { background: -webkit-linear-gradient(50deg, #520596 0%, #fc486b 80%); background: linear-gradient(50deg, #520596 0%, #fc486b 80%); }
  body:not(.home) .section-venture:before { content: none; } }

body:not(.home) .section-venture .section-title { margin: 0; padding: 7.5rem 0 9.5rem; position: relative; z-index: 1; }

@media screen and (max-width: 768px) { body:not(.home) .section-venture .section-title { color: #fff; padding: 3.5rem 0 1rem; } }

body:not(.home) .section-venture .left { position: relative; }

body:not(.home) .section-venture .left:before { background: url(../img/planets_form.png) no-repeat center 90%; bottom: 0; content: ''; left: 0; position: absolute; right: 0; top: 0; z-index: 0; }

@media screen and (max-width: 1100px) { body:not(.home) .section-venture .left:before { content: ''; } }

@media screen and (max-width: 1024px) { body:not(.home) .section-venture .left:before { background-size: contain; } }

@media screen and (max-width: 768px) { body:not(.home) .section-venture .left:before { background: url(../img/planets_form_768.png) no-repeat center -4.5rem; content: none; } }

@media screen and (max-width: 425px) { body:not(.home) .section-venture .left:before { background-image: none; } }

@media screen and (max-width: 768px) { body:not(.home) .section-venture .left { border: none; margin-top: -11.5rem; max-width: 100%; padding-top: 11.5rem; overflow: hidden; } }

@media screen and (max-width: 425px) { body:not(.home) .section-venture .left { margin: 0; padding: 0; } }

body:not(.home) .section-venture .left .planet-sq { animation-duration: 20s; height: 1.3rem; width: 1.3rem; }

@media screen and (max-width: 768px) { body:not(.home) .section-venture .left .planet-sq { height: 1.95rem; width: 1.95rem; } }

body:not(.home) .section-venture .left .planet-sq.sq-1 { animation-name: planetsFly-1a; left: 10%; top: 50%; }

@media screen and (max-width: 768px) { body:not(.home) .section-venture .left .planet-sq.sq-1 { left: 5%; top: 15%; } }

body:not(.home) .section-venture .left .planet-sq.sq-2 { animation-name: planetsFly-2a; left: 20%; top: 67%; }

@media screen and (max-width: 768px) { body:not(.home) .section-venture .left .planet-sq.sq-2 { left: 12%; top: 75%; } }

body:not(.home) .section-venture .left .planet-sq.sq-md { animation-name: planetsFly-3a; bottom: 5%; height: 2.29rem; width: 2.29rem; left: 30%; }

@media screen and (max-width: 768px) { body:not(.home) .section-venture .left .planet-sq.sq-md { left: 45%; } }

body:not(.home) .section-venture .left .planet-xs.xs-1 { animation-name: planetsFly-3a; height: 2.1rem; width: 2.1rem; left: 50%; top: 50%; }

@media screen and (max-width: 768px) { body:not(.home) .section-venture .left .planet-xs.xs-1 { height: 3.1rem; width: 3.1rem; top: 30%; } }

body:not(.home) .section-venture .left .planet-xs.xs-md { animation-name: planetsFly-2a; height: 3.8rem; width: 3.8rem; left: 93%; top: 68%; }

@media screen and (max-width: 768px) { body:not(.home) .section-venture .left .planet-xs.xs-md { height: 5.7rem; width: 5.7rem; left: 90%; top: 80%; } }

.contact-info { align-items: center; display: flex; justify-content: space-evenly; padding: 5rem 1rem; position: relative; }

@media screen and (max-width: 1100px) { .contact-info { align-items: flex-start; flex-direction: column; } }

@media screen and (max-width: 1024px) { .contact-info { padding: 3rem 1rem; } }

@media screen and (max-width: 768px) { .contact-info { align-items: center; flex-direction: row; justify-content: center; }
  .contact-info > div { margin: 0 3rem; } }

@media screen and (max-width: 425px) { .contact-info { flex-direction: column; } }

@media screen and (max-width: 1024px) { .contact-info svg.icon-satellite { height: 11.4rem; width: 13.1rem; } }

.contact-info .contact { color: #fff; font-size: 2.4rem; line-height: normal; padding-bottom: 1rem; text-align: left; }

@media screen and (max-width: 425px) { .contact-info .contact { font-size: 1.8rem; } }

.contact-info .contact p { padding: 2rem 0; }

@media screen and (max-width: 768px) { .contact-info .contact p { padding: 1rem 0; } }

.contact-info .contact b { font-weight: 900; }

@media screen and (max-width: 1440px) { .contact-info .contact b { display: block; } }

