.section-text { font-size: 1.8rem; line-height: 3rem; max-width: 56.5rem; margin: auto; }

@media screen and (min-width: 768px) { .section-text { padding-top: 2rem; } }

@media screen and (min-width: 1024px) { .section-text { padding-top: 10rem; } }

@media screen and (max-width: 425px) { .section-text { font-size: 1.4rem; line-height: normal; } }

.section-text .wrapper { padding: 0 2rem; }

.section-text .section-title { margin-bottom: 8rem; }

.section-text .section-title.dash:after { bottom: -4rem; }

.section-join { background: -webkit-linear-gradient(33deg, #520596 0%, #fc486b 70%); background: linear-gradient(33deg, #520596 0%, #fc486b 70%); height: 45rem; overflow: hidden; padding: 5rem 0; position: relative; }

.section-join:before { background: url(../img/planets.png) no-repeat center top; position: absolute; content: ''; left: 0; top: 0; bottom: 0; right: 0; }

@media screen and (max-width: 1024px) { .section-join:before { background-size: contain; } }

@media screen and (max-width: 768px) { .section-join:before { background-image: url(../img/planets_768.png); background-position: center -30px; background-size: initial; } }

@media screen and (max-width: 425px) { .section-join:before { background-image: url(../img/planets_425.png); background-position: 35px 0; } }

@media screen and (max-width: 425px) { .section-join { height: 23rem; padding: 0; } }

.section-join .planet-sq { animation-duration: 20s; height: 2.2rem; width: 2.2rem; }

@media screen and (max-width: 425px) { .section-join .planet-sq { display: block; } }

.section-join .planet-sq.sq-1 { animation-name: planetsFly-1a; left: 10%; top: 30%; }

@media screen and (max-width: 1024px) { .section-join .planet-sq.sq-1 { left: 5%; } }

@media screen and (max-width: 768px) { .section-join .planet-sq.sq-1 { top: 25%; } }

.section-join .planet-sq.sq-2 { animation-name: planetsFly-2a; left: 57%; top: 33%; }

@media screen and (max-width: 1024px) { .section-join .planet-sq.sq-2 { top: 20%; } }

@media screen and (max-width: 768px) { .section-join .planet-sq.sq-2 { left: 43%; top: 40%; } }

.section-join .planet-sq.sq-3 { animation-name: planetsFly-3a; bottom: 5%; left: 25%; }

@media screen and (max-width: 1024px) { .section-join .planet-sq.sq-3 { bottom: 15%; left: 15%; } }

.section-join .planet-sq.sq-4 { animation-name: planetsFly-1a; left: 65%; bottom: -2%; }

@media screen and (max-width: 1024px) { .section-join .planet-sq.sq-4 { bottom: 5%; } }

.section-join .planet-xs { height: 3.8rem; width: 3.8rem; }

@media screen and (max-width: 425px) { .section-join .planet-xs { display: block; } }

.section-join .planet-xs.xs-1 { animation-name: planetsFly-1a; left: 45%; top: 45%; }

@media screen and (max-width: 1024px) { .section-join .planet-xs.xs-1 { left: 50%; top: 40%; } }

@media screen and (max-width: 768px) { .section-join .planet-xs.xs-1 { left: 65%; top: 14%; } }

.section-join .planet-xs.xs-2 { animation-name: planetsFly-2a; right: 3%; top: 47%; }

@media screen and (max-width: 1024px) { .section-join .planet-xs.xs-2 { top: 35%; } }

@media screen and (max-width: 1024px) { .section-join .planet-xs.xs-2 { top: 50%; } }

.section-join .wrapper { align-items: center; display: flex; flex-direction: column; justify-content: space-evenly; height: 100%; }

.section-join .section-title { line-height: 6rem; max-width: 90rem; margin: auto; padding: 0 2rem; text-align: left; }

@media screen and (max-width: 768px) { .section-join .section-title { font-size: 4.8rem; margin: 0 auto 2rem; } }

@media screen and (max-width: 425px) { .section-join .section-title { font-size: 2.4rem; line-height: initial; margin-bottom: 0; padding-top: 1rem; } }

.section-join .button { height: 7.7rem; line-height: 7.3rem; margin: auto; max-width: 50.8rem; width: 100%; }

@media screen and (max-width: 768px) { .section-join .button { max-width: 38rem; } }

.section-join .button:hover .icon-arrow { stroke: #fff; }

.section-join .button .icon-arrow { stroke: #520596; }

.section-error { align-items: center; bottom: 0; display: flex; left: 0; position: absolute; right: 0; text-align: center; top: 0; }

.section-error .wrapper { display: flex; flex-direction: column; justify-content: center; height: 100vh; padding: 1rem 0; width: 100vw; }

@media all and (max-height: 570px) and (orientation: landscape) { .section-error .wrapper { justify-content: flex-start; overflow: auto; z-index: 1; } }

.section-error .section-title { margin-bottom: 4rem; }

.section-error .section-footer { padding: 3rem 0 0; }

.section-error h4 { color: #520596; font-size: 1.8rem; font-weight: 900; margin-bottom: 2rem; }

.section-error .section-body { align-items: center; display: flex; justify-content: center; overflow: hidden; min-height: 30rem; }

@media screen and (max-width: 1024px) and (orientation: landscape) { .section-error .section-body { flex: 1; } }

.section-error .section-body img { max-height: 52.2rem; }

@media screen and (max-width: 1024px) { .section-error .section-body img { max-height: 100%; } }

@media screen and (max-width: 425px) { .section-error .section-body img { max-width: 47rem; } }

.section-error .button { height: 7.7rem; line-height: 7.3rem; margin: auto; }

@media screen and (max-width: 320px) { .section-error .button svg.icon-arrow { left: -5px; width: 7rem; } }
